import ChoreDoneLog from "./ChoreDoneLog";
import NoChoreSelected from "./NoChoreSelected";
import Loading from "./Loading";
import DeleteChoreButton from "./DeleteChoreButton";
import ChoreAPI from "../data/ChoreAPI";
import Interval from "../data/Interval";
import QueryStringUtils from "../utils/QueryStringUtils";
import createReactInputChangeHandler from "../utils/createReactInputChangeHandler";
import errorAlert from "../utils/errorAlert";
class ChoreDetails extends React.Component{
	constructor(props){
		super(props); 
		this.state = {fetching:this.props.title!==null,error:false,updating:false,hasChore:this.props.title!==null,doneLog:false,title:this.props.title}
		this.onInputChange = createReactInputChangeHandler(this,this.onInputChangeCallback,(key,value)=>{
			if(value===""){
				return " ";
			}else if(key==="interval"){
				return Interval[value.toUpperCase()];
			}else{
				return value;
			} 
		});
		this.fetchData = this.fetchData.bind(this);
		this.onDelete = this.onDelete.bind(this);
		this.showDoneLog = this.showDoneLog.bind(this);
		this.showChore = this.showChore.bind(this);
	}
	componentDidMount(){
		if(QueryStringUtils.get("log")==="true"){
			this.setState({doneLog:true});
		}
		let title = this.props.title;
		if(title){
			this.fetchData(title);
		}
	}
	static getDerivedStateFromProps(newProps,prevState){
		console.log9
		if(newProps!==null&&prevState!==null&&newProps.title!==prevState.title){
			return {fetching:true}
		}
	}
	componentDidUpdate(prevProps){
		if(prevProps.title!==this.props.title){
			this.fetchData(this.props.title);
		}
	}
	onDelete(){
		QueryStringUtils.delete("chore")
		this.setState({hasChore:false});
	}
	showDoneLog(){
		QueryStringUtils.update("log",true);
		this.setState({doneLog:true})
	}
	showChore(){
		QueryStringUtils.update("log",false);
		this.setState({doneLog:false});
	}
	fetchData(name){
		ChoreAPI.getChore(name)
		.then(chore=>{
			const {title,person,time,interval,done} = chore;
			this.setState({title,person,time,interval,done,fetching:false,hasChore:true,error:false});
		})
		.catch((e)=>{
			this.setState({error:true});
			console.error(e);
		});
	}
	onInputChangeCallback(id,value,oldState){
		this.setState({updating:true});
		const error = (e)=>{
			errorAlert(e,"Could not update chore");
			this.setState({deleting:false,updating:false})
		}
		switch(id){
			case "title":
				ChoreAPI.renameChore(oldState.title,this.state.title)
					.then(()=>this.setState({updating:false}))
					.catch(error);
			break;
			case "done":
				ChoreAPI.updateChoreDone(this.state.title,this.state.done)
					.catch(error);
				break;	
			case "time":
				ChoreAPI.updateChoreTime(this.state.title,this.state.time)
					.then(()=>this.setState({updating:false}))
					.catch(error);
			break;
			case "person":
				ChoreAPI.updateChorePerson(this.state.title,this.state.person)
					.then(()=>this.setState({updating:false}))
					.catch(error);
			break;	
			case "interval":
				ChoreAPI.updateChoreInterval(this.state.title,this.state.interval)
					.then(()=>this.setState({updating:false}))
					.catch(error);
			break;	
		}
	}

	render(){
		if(this.state.error){
			return <p className="error">Could not fetch chore</p>;
		}else if(this.state.fetching){
			return <Loading/>;
		}else if(!this.state.hasChore){
			return <NoChoreSelected/>;
		}else if(this.state.doneLog){
			return [
			<div key="back">
				<button onClick={this.showChore}>Back</button>
			</div>,
			<div key="log">
				<ChoreDoneLog title={this.state.title} interval={this.state.interval}/>
			</div>];
		}else{
			return <div id="chore-details">
				{this.state.updating&&
					<p>Updating...</p>
				}
				<div>
					<label htmlFor="title">Name: </label>
					<input id ="title" defaultValue={this.state.title} onBlur={this.onInputChange}/>
				</div>
				<div>
					<label htmlFor="person">Person: </label>
					<input id ="person" defaultValue={this.state.person} onBlur={this.onInputChange}/>
				</div>	
				<div>
					<label htmlFor="time">Time: </label>
					<TimePicker id ="time" defaultValue={this.state.time}  onBlur={this.onInputChange} />
				</div>
				<div>
					<label htmlFor="interval">Interval: </label>
					<select id="interval" value={this.state.interval.toString().toLowerCase()} onChange={this.onInputChange}>
						<option value="daily">Daily</option>
						<option value="weekly">Weekly</option>
						<option value="monthly">Monthly</option>
					</select>
				</div>
				<div>
					<label htmlFor="done">Done: </label>
					<input id ="done" type="checkbox" checked={this.state.done} onChange={this.onInputChange}/>
				</div>
				<div><button onClick={this.showDoneLog}>Show Chore Log</button></div>
				<div>
					<DeleteChoreButton onDelete={this.onDelete} title={this.state.title}/>
				</div>

			</div>;
		}
	}
}
export default ChoreDetails;