import ChoreAPI from "../data/ChoreAPI";
import errorAlert from "../utils/errorAlert";
class AddChoreButton extends React.Component{
	constructor(props){
		super(props);
		this.onClick = this.onClick.bind(this);
	}
	onClick(){
		ChoreAPI.addChore()
			.then(()=>this.props.onAdd&&this.props.onAdd())
			.catch((e)=>errorAlert(e,"Could not add chore"));
	}
	render(){
		return <button id="add-chore-button" onClick={this.onClick}>Add Chore</button>
	}
}
export default AddChoreButton;