import ChoreListItem from "./ChoreListItem";
import ChoreAPI from "../data/ChoreAPI";
import Chore from "../data/Chore";
import Loading from "./Loading";
class ChoreList extends React.Component{
	constructor(props){
		super(props);
		this.state = {chores:[], fetching:true,error:false};
		ChoreAPI.registerListener(this.notify.bind(this));
		this.onClick = this.onClick.bind(this);
		this.fetchData = this.fetchData.bind(this);
		this.pollInterval = setInterval(this.fetchData,5*60*1000);
	}
	componentDidMount(){
		this.fetchData();
	}
	static getDerivedStateFromProps(newProps){
		if(newProps!==null&&this.props!==null&&newProps.person!==this.props.person){
			return {fetching:true}
		}
	}
	componentDidUpdate(oldProps){
		if(oldProps.person!==this.props.person){
			this.fetchData();
		}
	}
	componentWillUnmount(){
		clearInterval(this.pollInterval);
	}
	fetchData(){
		ChoreAPI.getChores(this.props.person)
			.then(chores=>this.setState({chores,fetching:false,error:false}))
			.catch((e)=>{
				console.error(e);
				this.setState({error:true});
			})
	}
	onClick(event,title){
		this.props.onItemClick&&this.props.onItemClick(title);
	}
	notify(event,data){
		if(["ADD","DELETE","UPDATE_DONE","RENAME","UPDATE_TIME"].includes(event) || 
			event === "UPDATE_PERSON" && this.state.person !== ""){
			switch(event){
				case "ADD":
					this.setState({chores:this.state.chores.concat(new Chore())});
				break;
				case "DELETE":
					this.setState({chores:this.state.chores.filter((chore)=>chore.title!==data.title)});
				break;
				case "UPDATE_DONE":
					this.setState({
						chores:this.state.chores.map((chore)=>{
							if(chore.title===data.title){
								let newChore = Object.assign(chore);
								newChore.done = data.done;
								return chore;
							}else{
								return chore;
							}
						})
					});
				break;
				case "UPDATE_TIME":
					this.setState({
						chores:this.state.chores.map((chore)=>{
							if(chore.title===data.title){
								let newChore = Object.assign(chore);
								newChore.time = data.time;
								return chore;
							}else{
								return chore;
							}
						})
					});
				break;
				case "UPDATE_PERSON":
					
					this.setState({
						chores:this.state.chores
							.map((chore)=>{
								if(chore.title===data.title){
									let newChore = Object.assign(chore);
									newChore.person = data.person;
									return chore;
								}else{
									return chore;
								}
							})
							.filter((chore)=>chore.person===data.person)
					});
				break;
				case "RENAME":
					this.setState({
						chores:this.state.chores.map((chore)=>{
							if(chore.title===data.oldName){
								let newChore = Object.assign(chore);
								newChore.title = data.newName;
								return chore;
							}else{
								return chore;
							}
						})
					});
				break;
						
			}
		}
	}
	render(){
		const chores = this.state.chores;
		if(this.state.error){
			return <p className="error">Could not fetch chores</p>;
		}else	if(this.state.fetching){
			return <Loading/>;
		}else if(chores.length === 0){
			return <p id="no-chores">There are no chores</p>
		}else{
			const listItems = chores.map((chore,index)=><ChoreListItem onClick={this.onClick} key={index} chore={chore}></ChoreListItem>);
			return <ul id="chore-list">{listItems}</ul>;
		}
	}
}
export default ChoreList;		