const registerServiceWorker = () => navigator.serviceWorker.register("service_worker.js");
const askNotificationPermission = () => new Promise((resolve, reject) => {
	const permissionResult = Notification.requestPermission((result)=>{	
		resolve(result);
	});
	if (permissionResult) {
		permissionResult.then(resolve, reject);
	}
});
const base64ToUint8Array = (base64)=>{
	let binaryString =  window.atob(base64);
	let length = binaryString.length;
	let bytes = new Uint8Array(length);
	for (let i = 0; i < length; i++){
		bytes[i] = binaryString.charCodeAt(i);
	}
	return bytes;
}
const subscribePush = registration => registration.pushManager.subscribe({
	userVisibleOnly: true,
	applicationServerKey: base64ToUint8Array("BNr5f5wZ5Gg63fNfZ7U+/xkwrraXXgr1N14lkVXhWE0iN5sR3qdGds4vkFZSo31UVWWNFMaqEtnqhwJKVAV1PRs=")
});
const sendSubscriptionToServer = subscription => {
	const key = subscription.getKey("p256dh");
	const auth = subscription.getKey("auth");
	const body = {
		endpoint: subscription.endpoint,
		key: btoa(String.fromCharCode.apply(null, new Uint8Array(key))),
		auth: btoa(String.fromCharCode.apply(null, new Uint8Array(auth)))
	};
	return fetch("https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerSubscribe", {
		method: "POST",
		body: JSON.stringify(body)
	}).then(response=>{
		if(response.ok){
			return response.json()
		}else{
			response.text().then(console.error)
		}
	})
};
let setupServiceWorker = async ()=>{
	let registration = await registerServiceWorker();
	if("PushManager" in window && "showNotification" in ServiceWorkerRegistration.prototype && Notification.permission !=="granted"){
		let permissionResult = await askNotificationPermission();
		if(permissionResult ==="granted"){
			subscribePush(registration).then(sendSubscriptionToServer);
		}
	}
}
export default setupServiceWorker;