import Interval from "./Interval";
import dateToUTC from "../utils/dateToUTC"
class Chore{
	constructor(title = " ",person = " ",time = dateToUTC(new Date()),interval = Interval.DAILY,done = false){
		this.title = title;
		this.person = person;
		this.time = time;
		this.interval = interval;
		this.done = done;
	}
	
	toString(){
		return `title = ${this.title} person = ${this.person} interval = ${this.interval} time = ${this.time} done = ${this.done}`;
	}
	static fromJson(json){
		const time = dateToUTC(new Date());
		time.setHours(json.time[0]);
		time.setMinutes(json.time[1]);
		time.setSeconds(json.time[2]); 
		return new Chore(
			json.title, 
			json.person, 
			new Date(Date.UTC(time.getFullYear(),time.getMonth(),time.getDate(),time.getHours(),time.getMinutes(),time.getSeconds())), 
			Interval[json.interval], 
			json.done)
	}
}

export default Chore;