import formatDate from "../utils/formatDate";
class ChoreListItem extends React.Component{
	constructor(props){
		super(props);
		this.onClick = this.onClick.bind(this);
		this.onKeyPress = this.onKeyPress.bind(this);
	}
	onClick(event){
		this.props.onClick(event,this.props.chore.title);
	}
	onKeyPress(event){
		if(event.keyCode===13){
			this.props.onClick(event,this.props.chore.title);
		}
	}
	shouldComponentUpdate(newProps){
		return this.props.chore.done===newProps.chore.title||
		this.props.chore.title===newProps.chore.title||
		this.props.chore.time.getTime() === newProps.chore.time.getTime();
	}
	render(){
		return <li onClick={this.onClick} onKeyPress={this.onKeyPress}>
			<div className="list-item-title">{this.props.chore.title}</div>
			<input type="checkbox" className="list-item-done" checked={this.props.chore.done/*?"checked":""*/} readOnly disabled/>
			<div className="list-item-time">{formatDate(this.props.chore.time,"h:mm aa")}</div>
		</li>
	}
}
export default ChoreListItem;