import ChoreDetails from "./ChoreDetails";
import ChoreList from "./ChoreList";
import AddChoreButton from "./AddChoreButton";
import QueryStringUtils from "../utils/QueryStringUtils";
class App extends React.Component{
	constructor(props){
		super(props);
		this.state={person:QueryStringUtils.get("person")||"",choreTitle:QueryStringUtils.get("chore")};
		this.onSubmit = this.onSubmit.bind(this);
		this.onChoreChange = this.onChoreChange.bind(this);
	}
	onSubmit(event,person){
		QueryStringUtils.update("person",person);
		this.setState({person});
	}
	onChoreChange(title=" "){
		QueryStringUtils.update("chore",title);
		this.setState({choreTitle:title});
	}
	render(){
		return [
			<div key="list" id="chore-list-frame">
				<SearchBar id="search-bar" placeholder="Search by person" onSubmit={this.onSubmit} value={this.state.person}/>
				<AddChoreButton onAdd={this.onChoreChange}/>
				<ChoreList onItemClick={this.onChoreChange}person={this.state.person}/>
			</div>,
			<div key="details" id="chore-details-frame">
				<ChoreDetails title={this.state.choreTitle}/>
			</div>]
	}	
}
export default App;