export default (that,callback=()=>null,formatter=(a,b)=>b) =>
(event)=>{
	let id = event.target.id;
	let value = event.target.type ==="checkbox"?event.target.checked:event.target.value;
	let oldState = Object.assign({},that.state);
	if(oldState[id] !== value){
		that.setState({
			[id]:formatter(id,value)
		},()=>callback.call(that,id,value,oldState));
	}
}