import ChoreAPI from "../data/ChoreAPI";
import Interval from "../data/Interval";
import DoneStatus from "./DoneStatus";
import Loading from "./Loading";
import formatDate from "../utils/formatDate";
class ChoreDoneLog extends React.Component{
	constructor(props){
		super(props);
		this.state = {fetching:true,error:false,page:0};
		this.onPrevious = this.onPrevious.bind(this);
		this.onNext = this.onNext.bind(this);
	}
	componentDidMount(){
		this.fetchData();
	}
	fetchData(){
		ChoreAPI.getDoneLog(this.props.title)
			.then(a=>a.reverse())
			.then(doneLog=>this.setState({doneLog,fetching:false}))
			.catch((e)=>{
			console.error(e);
			this.setState({error:true});
		})
	}
	onPrevious(){
		this.setState({page:this.state.page-1})
	}
	onNext(){
		this.setState({page:this.state.page+1});
	}
	render(){
		if(this.state.error){
			return <p className="error">Could not fetch done log</p>;
		}else	if(this.state.fetching){
			return <Loading/>
		}else if(this.state.doneLog.length===0){
			return <p>There are no entries in the log</p>;
		}else{
			let index = this.state.page*10;
			let doneLog = this.state.doneLog.slice(index,index+9).map((e,i)=>{
				let title;
				let date = new Date();
				//TODO change to use custom formatting function
				switch(this.props.interval){
					case Interval.DAILY:
						date.setDate(date.getDate()-(i+1))
						title = formatDate(date,"M/D/YYYY");
					break;
					case Interval.WEEKLY:
						date.setDate(date.getDate()-date.getDay()-(i+1)*7)
						date.setHours(0, 0, 0, 0);
						title = "Week of "+formatDate(date,"M/D/YYYY");
					break;
					case Interval.MONTHLY:
						date.setDate(1);
						date.setHours(0, 0, 0, 0);
						date.setMonth(date.getMonth()-(i+1))
						title = formatDate(date,"MMMM");
					break;
				}
				return <DoneStatus done={e} title={title} key={i}/>
			});
			return [
				<div key="done-log" id="done-log">{doneLog}</div>,
				this.state.doneLog.length>10 &&
				<div key="buttons">
					{this.state.page>0 &&
						<button onClick={this.onPrevious}>Previous</button>}
					{this.state.page<this.state.doneLog.length-1&&
						<button onClick={this.onNext}>Next</button>}
				</div>];
		}
	}
}
export default ChoreDoneLog ;