import Chore from "./Chore";
const ChoreAPI ={
	listeners: [],
	handleErrors(response) {
		if(response.ok){
			return response;
		}else{
			return response.text().then(a => Promise.reject(a));
		}	
	},
	getDoneLog(title) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerGetDoneLog?title=${encodeURIComponent(title)}`).then(this.handleErrors).then(response => response.json())
	},
	getChore(title) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerGetChore?title=${encodeURIComponent(title)}`).then(this.handleErrors).then(response => response.json()).then(json => Chore.fromJson(json))
	},
	getChores(person="") {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerGetChores?person=${encodeURIComponent(person)}`).then(this.handleErrors).then(t => t.json()).then(chores => chores.map(json => Chore.fromJson(json)))
	},
	addChore() {
		return fetch("https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerAddChore", {
			method: "POST"
		}).then(this.handleErrors).then(() => this.notifyListeners("ADD"))
	},
	deleteChore(title) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerDeleteChore?title=${encodeURIComponent(title)}`, {
			method: "DELETE"
		}).then(this.handleErrors).then(() => this.notifyListeners("DELETE",{title}))
	},
	updateChorePerson(title, person) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerUpdateChorePerson?title=${encodeURIComponent(title)}&person=${encodeURIComponent(person)}`, {
			method: "POST"
		}).then(this.handleErrors).then(() => this.notifyListeners("UPDATE_PERSON",{title,person}))
	},
	updateChoreDone(title, done) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerUpdateChoreDone?title=${encodeURIComponent(title)}&done=${encodeURIComponent(done)}`, {
			method: "POST"
		}).then(this.handleErrors).then(() => this.notifyListeners("UPDATE_DONE",{title,done}))
	},
	updateChoreInterval(title, interval) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerUpdateChoreInterval?title=${encodeURIComponent(title)}&interval=${encodeURIComponent(interval.toString().toUpperCase())}`, {
			method: "POST"
		}).then(this.handleErrors).then(() => this.notifyListeners("UPDATE_INTERVAL",{title,interval}))
	},
	updateChoreTime(title,time) {
		const json = JSON.stringify({
			title,
			time
		});

		return fetch("https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerUpdateChoreTime", {
			method: "POST",
			body: json
		}).then(this.handleErrors).then(() => this.notifyListeners("UPDATE_TIME",{title,time}))
	},
	renameChore(oldName, newName) {
		return fetch(`https://rdg2qx0aeg.execute-api.us-east-1.amazonaws.com/prod/ChoreManagerRenameChore?oldName=${encodeURIComponent(oldName)}&newName=${encodeURIComponent(newName)}`, {
			method: "POST"
		}).then(this.handleErrors).then(() => this.notifyListeners("RENAME",{oldName,newName}))
	},
	registerListener(listener) {
		this.listeners.push(listener);
		return this.length - 1
	},
	unregisterListener(index) {
		this.listeners.splice(index, 1);
	},
	notifyListeners(event,data) {
		this.listeners.forEach(listener => listener(event,data));
	}
};
export default ChoreAPI;