/*adapted from https://github.com/date-fns/date-fns/blob/master/src/format/_lib/formatters/index.js*/
const formatters = {
	// Month: 1, 2, ..., 12
	"M": (date)=>{
		return date.getMonth() + 1
	},

	// Month: 01, 02, ..., 12
	"MM": (date)=>{
		return (date.getMonth()+1).toString().padLeft(2)
	},

	// Month: Jan, Feb, ..., Dec
	"MMM": (date)=>{
		return ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"][date.getMonth()]
	},

	// Month: January, February, ..., December
	"MMMM": (date)=>{
		return ["January","February","March","April","May","June","July","August","September","October","November","December"][date.getMonth()]
	},

	// Day of month: 1, 2, ..., 31
	"D": (date)=>{
		return date.getDate()
	},

	// Day of month: 01, 02, ..., 31
	"DD": (date)=>{
		return date.getDate().toString().padLeft(2)
	},

	// Day of week: Sun, Mon, ..., Sat
	"ddd": (date)=>{
		return ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"][date.getDay()]
	},

	// Day of week: Sunday, Monday, ..., Saturday
	"dddd": (date)=>{
		return ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"][date.getDay()]
	},

	// Day of week: 0, 1, ..., 6
	"d": (date)=>{
		return date.getDay()
	},

	// Day of ISO week: 1, 2, ..., 7
	"E": (date)=>{
		return date.getDay() || 7
	},

	// Year: 00, 01, ..., 99
	"YY": (date)=>{
		return date.getFullYear().toString().padLeft(4).substr(2)
	},

	// Year: 1900, 1901, ..., 2099
	"YYYY": (date)=>{
		return date.getFullYear().toString().padLeft(4)
	},
	// Hour: 0, 1, ... 23
	"H": (date)=>{
		return date.getHours()
	},

	// Hour: 00, 01, ..., 23
	"HH": (date)=>{
		return date.getHours().toString().padLeft(2)
	},

	// Hour: 1, 2, ..., 12
	"h": (date)=>{
		var hours = date.getHours()
		if (hours === 0) {
			return 12
		}else if (hours > 12) {
			return hours % 12
		} else {
			return hours
		}
	},

	// Hour: 01, 02, ..., 12
	"hh": (date)=>{
		return formatters["h"](date).toString().padLeft(2)
	},

	// Minute: 0, 1, ..., 59
	"m": (date)=>{
		return date.getMinutes()
	},

	// Minute: 00, 01, ..., 59
	"mm": (date)=>{
		return date.getMinutes().toString().padLeft(2)
	},

	// Second: 0, 1, ..., 59
	"s": (date)=>{
		return date.getSeconds()
	},

	// Second: 00, 01, ..., 59
	"ss": (date)=>{
		return date.getSeconds().toString().padLeft(2)
	},

	// AM, PM
	"A": (date)=>{
		return date.getHours()<12?"AM":"PM";
	},

	// am, pm
	"a": (date)=>{
		return date.getHours()<12?"am":"pm";
	},

}

let formatDate = (date,formatString)=>{
	let formatter = formatters[formatString];
	return formatter(date);
}
export default formatDate;