import ChoreAPI from "../data/ChoreAPI";
import errorAlert from "../utils/errorAlert";
class DeleteChoreButton extends React.PureComponent{
	constructor(props){
		super(props);
		this.onClick = this.onClick.bind(this);
	}
	onClick(){
		swal({
			title:"Are you sure you want to delete this chore?",
			showCancelButton:true
		}).then((result)=>{
			if(result){
				ChoreAPI.deleteChore(this.props.title)
				.catch((e)=>errorAlert(e,"Could not delete chore"));
				this.props.onDelete&&this.props.onDelete();
			}
		})
		
	}
	render(){
		return <button id="delete-chore-button" onClick={this.onClick}>Delete Chore</button>
	}
}
export default DeleteChoreButton;