class Enum{
	constructor(...values){
		for(let i=0;i<values.length;i++){
			this[values[i]]=new Enum.Item(values[i],i);
		}
	}
}
Enum.Item = class{
	constructor(name,value){
		this.name = name;
		this.value = value;
	}
	[Symbol.toPrimitive](hint){
		if(hint==="number"){
			return this.value;
		}
		return this.toString();
	}
	toString(){
		return this.name.toString();
	}	
	toJSON(){
		return this.name;
	}

}
export default Enum;